// Node libraries
import * as fs from 'fs';
import * as stream from 'stream';

// Shared libraries
import * as Util from '@terrencecrowley/util';
import * as Context from '@terrencecrowley/context';
import * as Storage from '@terrencecrowley/storage';
import * as FSM from '@terrencecrowley/fsm';

const FSM_SAVING: number = FSM.FSM_CUSTOM1;
const FSM_UPLOADING: number = FSM.FSM_CUSTOM2;

// Current 'kind's of log entries in use:
//  'event': some event occurred (event property)
//  'error': some error happened
//  'value': some internal state variable has some value (name, value properties)
//  'sync': synchronous (blocking) time interval (ms property in milliseconds)
//  'async': elapsed time interval (ms property in milliseconds)

// LogManager receives logging requests (in object form) and buffers them to some internal size
// limit and then saves them out as a storage blob.
// Blobs are written out in a form that lets a separate offline process aggregate them as necessary.
// Entries are written as CSV entries with the first lines giving schema definitions for the
// entries in the file and subsequent entries providing the data. This allows both a flexible
// logging format as well as a compact representation.
//
//  Schema are of the form:
//    'S',SchemaId,P1,P2,P3
//
//  Entries are:
//    'L',SchemaId,V1,V2,V3
//
//  Where "SchemaId" is a small integer and a line that starts with S defines the
//  schema and a line that starts with L references it.
//

// Max size of log file (approximated)
Context.setDefaults({ MaxLogFileLines: 10000 });

// A Schema maps property value to the index in the CSV line, starting with index 2
type Schema = { [prop: string]: number };

// A SchemaMapping indexes by a sequence ID for the Schema
type SchemaIndex = { [key: number]: Schema };

// A schema mapping maps a hash value for the schema to a small integer ID
type SchemaMapping = { [key: number]: number };

interface SchemaResult
{
  schemaId: number;
  schema: Schema;
  csv: string | null;
}

class SchemaManager
{
  index: SchemaIndex;
  map: SchemaMapping;
  nextid: number;

  constructor()
  {
    this.clear();
  }

  clear(): void
  {
    this.index = {};
    this.map = {};
    this.nextid = 0;
  }

  private extractSchema(o: any): Schema
  {
    let s: Schema = {};
    let i: number = 2;  // index in CSV line for this value (starts with 'L,schemaid')
    for (let p in o) if (o.hasOwnProperty(p))
      s[p] = i++;
    return s;
  }

  add(o: any): SchemaResult
  {
    let s = this.extractSchema(o);
    let h = Util.hashObject(s);
    let id = this.map[h];
    if (id === undefined)
    {
      id = this.nextid++;
      this.map[h] = id;
      this.index[h] = s;

      // Build schema string
      let sa: string[] = [];
      sa.push('S');
      sa.push(String(this.nextid-1));
      for (let p in s) if (s.hasOwnProperty(p))
        sa[s[p]] = p;
      return { schemaId: id, schema: s, csv: sa.join(',') };
    }
    return { schemaId: id, schema: s, csv: null };
  }
}

class TmpBlob extends Storage.StorageBlob
{
  path: string;
  fsm: FSM.Fsm;

  constructor(id: string, path: string)
  {
    super(id);
    this.path = path;
    this.setDirty();
    this.fsm = new FSM.Fsm('Log.TmpBlob');
  }

  asFile(): string
  {
    return this.path;
  }

  endSave(br: Storage.BlobRequest)
  {
    if (! this.isDirty())
      new FsmUnlink(this.id);
    this.fsm.setState(FSM.FSM_DONE);
  }
}

class LogInstance
{
  schemas: string[];
  log: string[];

  constructor()
  {
    this.schemas = [];
    this.log = [];
  }

  addSchema(s: string): void
  {
    this.schemas.push(s);
  }

  addLog(s: string): void
  {
    this.log.push(s);
  }

  full(): boolean
  {
    return (this.log.length + this.schemas.length) > Context.xnumber('MaxLogFileLines');
  }
}

class FsmSaveTmp extends FSM.Fsm
{
  path: string;
  lines: string[][];
  curArray: number;
  curIndex: number;
  stream: stream.Writable;

  constructor(path: string, schemas: string[], log: string[])
  {
    super('FsmSaveTmp');
    this.path = path;
    this.lines = [ schemas, log ];
    this.curArray = 0;
    this.curIndex = 0;
    this.stream = null;
  }

  start(): void
  {
    this.stream = fs.createWriteStream(this.path);
    this.stream.on('error', this.onError);
    this.stream.on('finish', this.onFinish);
    this.write();
  }

  write(): void
  {
    if (this.stream)
    {
      let bOK: boolean = true;

      for (; bOK && this.curArray < this.lines.length; this.curArray++, this.curIndex = 0)
      {
        let a = this.lines[this.curArray];
        for (; bOK && this.curIndex < a.length; this.curIndex++)
          bOK = this.stream.write(a[this.curIndex], 'utf8') && this.stream.write('\n', 'utf8');

        if (! bOK)
          break;
      }

      if (this.curArray == this.lines.length)
        this.stream.end();
      else 
        this.stream.once('drain', this.onDrain);
    }
  }

  onFinish(): void
  {
    if (this.stream)
    {
      this.stream.destroy();
      this.stream = null;
    }
    if (! this.done)
      this.setState(FSM.FSM_DONE);
  }

  onDrain(): void
  {
    this.write();
  }

  onError(): void
  {
    this.setState(FSM.FSM_ERROR);
    this.onFinish();
  }

  tick(): void
  {
    if (this.ready)
    {
      switch (this.state)
      {
        case FSM.FSM_STARTING:
          this.setState(FSM.FSM_PENDING);
          this.start();
          break;
      }
    }
  }
}

class FsmUpload extends FSM.Fsm
{
  sm: Storage.StorageManager;
  log: LogInstance;
  name: string;
  path: string;
  fsmTmp: FsmSaveTmp;
  blob: TmpBlob;

  constructor(sm: Storage.StorageManager, name: string, log: LogInstance)
  {
    super('Log.FsmUpload');
    this.sm = sm;
    this.name = name;
    this.path = `logs/${name}`;
    this.log = log;
  }

  tick(): void
  {
    if (this.ready)
    {
      switch (this.state)
      {
        case FSM.FSM_STARTING:
          this.setState(FSM_SAVING);
          this.fsmTmp = new FsmSaveTmp(this.path, this.log.schemas, this.log.log);;
          this.waitOn(this.fsmTmp);
          break;

        case FSM_SAVING:
          this.setState(FSM_UPLOADING);
          this.blob = new TmpBlob(this.name, this.path);
          this.blob.checkSave(this.sm);
          this.waitOn(this.blob.fsm);
          break;

        case FSM_UPLOADING:
          new FsmUnlink(this.path);
          this.setState(FSM.FSM_DONE);
          break;
      }
    }
  }
}

class FsmUnlink extends FSM.Fsm
{
  path: string;
  err: any;

  constructor(path: string)
  {
    super('FsmUnlink');
    this.path = path;
    this.err = null;
  }

  tick(): void
  {
    if (this.ready && this.state === FSM.FSM_STARTING)
    {
      this.setState(FSM.FSM_PENDING);
      fs.unlink(this.path, (err: any) => {
          if (err)
          {
            this.setState(FSM.FSM_ERROR);
            this.err = err;
          }
          else
            this.setState(FSM.FSM_DONE);
        });
    }
  }
}

class FsmMkdir extends FSM.Fsm
{
  dir: string;
  bAllowExist: boolean;

  constructor(dir: string, bAllowExist: boolean = true)
  {
    super('FsmMkdir');
    this.dir = dir;
    this.bAllowExist = true;
  }

  tick(): void
  {
    if (this.ready && this.state == FSM.FSM_STARTING)
    {
      this.setState(FSM.FSM_PENDING);
      fs.mkdir(this.dir, 0o777, (err: any) => {
          // ignore EEXISTS errors
          if (err === null || (this.bAllowExist && err.code === 'EEXIST'))
            this.setState(FSM.FSM_DONE);
          else
            this.setState(FSM.FSM_ERROR);
        });
    }
  }
}

function csvField(a: any): string
{
  let s = String(a);
  let i = s.indexOf(',');
  if (i == -1)
    return s;
  let sa = s.split(',');
  return sa.join('\\,');
}

class LogManager
{
  id: string;
  count: number;
  nextid: number;
  sm: Storage.StorageManager;
  schemaManager: SchemaManager;
  curLog: LogInstance;

  constructor(sm: Storage.StorageManager)
  {
    this.id = Util.createGuid();  // guid for this server instance
    this.count = 0;
    this.nextid = 0;              // to make next log file name unique
    this.sm = sm;
    this.schemaManager = new SchemaManager();
    this.curLog = new LogInstance();
    new FsmMkdir('logs');
    theLog = this;
  }

  log(o: any, verbosity: number): void
  {
    // Show some restraint
    if (verbosity > Context.xnumber('verbosity'))
      return;

    // Inject some standard properties
    o._time = Util.Now();
    o._count = this.count++;
    if (o.kind === undefined)
      o.kind = 'misc';

    // Add to current aggregate
    let sr: SchemaResult = this.schemaManager.add(o);
    if (sr.csv)
      this.curLog.addSchema(sr.csv);
    let sa: string[] = [];
    sa.push('L');
    sa.push(String(sr.schemaId));
    for (let p in o) if (o.hasOwnProperty(p))
      sa[sr.schema[p]] = csvField(o[p]);
    this.curLog.addLog(sa.join(','));
    if (this.curLog.full())
      this.rotateLog();
  }

  private rotateLog(): void
  {
    new FsmUpload(this.sm, `Log_${Util.Now()}_${this.nextid++}_${this.id}.csv`, this.curLog);
    this.curLog = new LogInstance();
  }

  dump(): void
  {
    this.rotateLog();
  }
}

export function dump(): void
{
  if (theLog) theLog.dump();
}

export function log(o: any, verbosity: number = 0): void
{
  if (theLog)
    theLog.log(o, verbosity);
  else
    console.log(JSON.stringify(o));
}

export function event(o: any, verbosity: number = 0): void
{
  if (typeof o === 'string') o = { event: o };
  o.kind = 'event';
  log(o, verbosity);
}

export function error(o: any): void
{
  if (typeof o === 'string') o = { message: o };
  o.kind = 'error';
  log(o);
}

export function value(o: any, verbosity: number = 0): void
{
  o.kind = 'value';
  log(o, verbosity);
}

export class Timer
{
  o: any;
  verbosity: number;
  elapsed: Util.Elapsed;

  constructor(o: any, kind: string, verbosity: number = 0)
  {
    if (typeof o === 'string') o = { event: o };
    this.o = o;
    this.o.kind = kind;
    this.elapsed = new Util.Elapsed();
  }

  log(): void
  {
    this.elapsed.end();
    this.o.ms = this.elapsed.ms();
    log(this.o, this.verbosity);
  }
}

export class AsyncTimer extends Timer
{
  constructor(o: any, verbosity: number = 0)
  {
    super(o, 'async', verbosity);
  }
}

export class SyncTimer extends Timer
{
  constructor(o: any, verbosity: number = 0)
  {
    super(o, 'sync', verbosity);
  }
}

let theLog: LogManager = null;

export function setStorage(sm: Storage.StorageManager): void
{
  if (theLog == null)
    theLog = new LogManager(sm);
  else
    theLog.sm = sm;
}
