import * as Util from '@terrencecrowley/util';

export let verbosity: number = 0;

export type ContextValues = { [key: string]: any };

let Values: ContextValues = {};
let Defaults: ContextValues = {};

export function setValues(o: ContextValues): void
{
  Values = Util.shallowAssignImmutable(Values, o);
  verbosity = xnumber('verbosity');
}

export function setDefaults(o: ContextValues): void
{
  Defaults = Util.shallowAssignImmutable(Defaults, o);
  verbosity = xnumber('verbosity');
}

export function xvalue(prop: string): any
{
  let v: any = Values[prop];
  if (v === undefined && process && process.env)
    v = process.env[prop.toUpperCase()];
  if (v === undefined)
    v = Defaults[prop];
  return v;
}

export function xset(prop: string): boolean
{
  let v: any = xvalue(prop);
  return v !== undefined;
}

export function xflag(prop: string): boolean
{
  let v: any = xvalue(prop);
  if (v === undefined || v === null)
    return false;
  return Number(v) != 0;
}

export function xnumber(prop: string): number
{
  let v: any = xvalue(prop);
  if (v === undefined || v === null)
    return 0;
  return Number(v);
}

export function xstring(prop: string): string
{
  let v: any = xvalue(prop);
  if (v === undefined || v === null)
    return null;
  return String(v);
}
